body {
  background-color: #f5f5f5;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14;
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

svg {
  display: block;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  text-decoration: none;
}

.no-scroll {
  overflow: hidden;
}

.menu-container {
  width: 130px;
  height: 55px;
  z-index: 999;
  background-color: #fff;
  flex-direction: column;
  padding: 16px 70px 16px 16px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-container .menu-button {
  color: var(--primary-white-color);
  position: absolute;
  top: 4px;
  right: 4px;
}

.menu-button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 0;
  padding: 0;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), fill .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
}

@media screen and (max-width: 767px) {
  .menu-button {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .menu-button {
    display: none;
  }
}

.menu-container.is-open .nav .menu-container.is-open .nav {
  display: block;
}

.container {
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

.container--header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.container--offerings {
  background-color: #ffe550;
  border-top-left-radius: 50%;
}

.mobile-menu-btn {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

@media screen and (min-width: 768px) {
  .mobile-menu-btn {
    display: none;
  }
}

.header-wrapper {
  align-items: center;
  display: flex;
}

@media screen and (max-width: 767px) {
  .header-wrapper {
    display: none;
  }
}

.nav__list {
  align-items: center;
  margin-right: 30px;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 28px;
}

.nav__link {
  color: #000;
  padding-top: 28px;
  padding-bottom: 28px;
}

.nav__link:hover, .nav__link:focus {
  color: #bbf330;
}

.nav__link.current {
  color: #bbf330;
  -webkit-text-decoration: underline #bbf330;
  text-decoration: underline #bbf330;
}

.logo {
  padding: 10px 0;
}

.hero {
  color: #2c2c2c;
}

.hero__title {
  margin-bottom: 28px;
  font-size: 28px;
  line-height: 1.18;
}

.hero__description {
  margin-bottom: 28px;
  line-height: 1.5;
}

.btn {
  color: #2c2c2c;
  cursor: pointer;
  background-color: #bbf330;
  border: none;
  border-radius: 30px;
  padding: 6px 18px;
  font-size: 13px;
